<h1 id="odoo-12" tabindex="-1"><a class="header-anchor" href="#odoo-12" aria-hidden="true">#</a> Odoo 12</h1>
<h2 id="description" tabindex="-1"><a class="header-anchor" href="#description" aria-hidden="true">#</a> Description</h2>
<p>Odoo est un CRM assez complet qui permet de faire plein de choses notament la gestion d'entreprise etc.</p>
<h2 id="installation-odoo-13" tabindex="-1"><a class="header-anchor" href="#installation-odoo-13" aria-hidden="true">#</a> Installation Odoo 13</h2>
<h3 id="environnement" tabindex="-1"><a class="header-anchor" href="#environnement" aria-hidden="true">#</a> Environnement</h3>
<p>Nous allons installer Odoo sur un serveur virtuel sous VirtualBox avec Ubuntu 18.04 server comme OS.<br>
Au moment de l'installation il faudra veiller à ne pas utilise &quot;odoo&quot; comme nom d'utilisateur.<br>
Nous allons installer <strong>Openssh-server</strong> pour pouvoir travailler plus facilement.</p>
<h2 id="installation-online" tabindex="-1"><a class="header-anchor" href="#installation-online" aria-hidden="true">#</a> Installation online</h2>
<ol>
<li>Créer un compte chez <a href="http://alwaysdata.com/" target="_blank" rel="noopener noreferrer">Alwaysdata<OutboundLink/></a>;</li>
<li>Créer le site odoo à partir de l'installateur, il faudra faire une migration de posgrestsql avant de procéder à l'installation</li>
<li>Les identifiants par défaut sont : <strong>admin/admin</strong></li>
<li>Changer la langue</li>
<li>Configurer le serveur mail <a href="https://www.odoo.com/fr_FR/forum/aide-1/question/incoming-outgoing-mail-configuration-44842" target="_blank" rel="noopener noreferrer">lien<OutboundLink/></a></li>
</ol>
<h2 id="bugs" tabindex="-1"><a class="header-anchor" href="#bugs" aria-hidden="true">#</a> Bugs</h2>
<h3 id="editer-des-contacts" tabindex="-1"><a class="header-anchor" href="#editer-des-contacts" aria-hidden="true">#</a> Editer des contacts</h3>
<p>Pour pouvoir créer et éditer des utilisateurs il faut :</p>
<ul>
<li>Activer le <strong>mode développeur</strong> (dans le menu de configuration)</li>
<li>Aller dans la section <strong>Facture</strong> puis <strong>Configuration</strong></li>
<li>Installer un plan comptable (HOADA pour l'Afrique de l'ouest)</li>
</ul>
